import router from './router'
import store from './store'
import vue from 'vue'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css'// Progress 
import vFetch from './api/login'
import Cookies from 'js-cookie';
import  tools from './utils/tools'
let clearLogout = tools.clearLogout;
// register global progress.
const whiteList = ['/login', '/authredirect']// 
router.beforeEach((to, from, next) => {
  NProgress.start() // Progress
  let isRouter = 0;
  if (store.getters.token) { // token
    if (to.path === '/login') {
      clearLogout();
      next({ path: '/' })
    } else if (from.path === '/login') {
      vFetch.postUserinfo({ token: store.state.user.token, username: store.state.user.username }).then(res => {
        if (res.status === 200) {
          store.state.routers = {};
          // 
          store.commit('SET_USERINFO', res.res);
          const jurisdiction = res.res.jurisdiction;
          // 
          store.dispatch('GenerateRoutes', { 'jurisdiction': jurisdiction }).then(res2 => { // 
            router.addRoutes(res2) // 
            next() // hack addRoutes
          })
        } else if (res.status === 0) {
          //  
          clearLogout();
          router.push({ path: '/login' })
        }
      }).catch(err => {
        if (err) {
          clearLogout()
          router.push({ path: '/login' })
        }
      })
    } else {
      if (!isRouter) {
        var jurisdiction = store.state.user.userinfo.jurisdiction;
        // 
        store.dispatch('GenerateRoutes', { 'jurisdiction': jurisdiction }).then(res2 => { // 
          router.addRoutes(res2) // 
          next(to.path) // hack addRoutes
        })
        isRouter++;
      }

      //   
      store.dispatch('getNowRoutes', to);
    }
    
    next();
  } else {
    if (whiteList.indexOf(to.path) !== -1) { // 
      next()
    } else {
      clearLogout()
      next('/login') // 
      NProgress.done() // hash hash  afterEach hack pshistory
    }
  }
})

router.afterEach(() => {
  NProgress.done() // Progress
})



